.rs-hide {
  display: none;
}

.chosen-container {
  width: 100% !important;
}

.autocomplete-suggestions {
  overflow: auto;
  border: 1px solid #ccc;
  background: #fff;
}

.autocomplete-suggestion {
  display: block;
  overflow: hidden;
  padding: 5px 15px;
  white-space: nowrap;

  strong {
    color: #029cca;
    font-weight: normal;
  }
}